export default {
    namespaced: true,
    state: {
        data: [],
        isLoaded: false
    },
    mutations: {
        setData : function (state, payload) {
            state.data = payload.map(s => { return s.data}).sort((a,b)=>{
                return a.dexNumber - b.dexNumber; 
            })
            state.isLoaded = true
        }
    }
}